import React from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  selectCartItems,
  selectTotalPrice,
  selectTotalItemsCount,
} from "../../redux/cart/cartSelectors";
import { clearItems } from "../../redux/cart/cartSlice";
import CartItem from "../CartItem/CartItem";
import SvgCartClear from "../svg/SvgCartClear";
import SvgCart from "../svg/SvgCart";
import SvgGoBack from "../svg/SvgGoBack";

type Item = {
  id: string;
  count: number;
  title: string;
  imageUrl: string;
  price: number;
  type: string;
  size: number;
};

const Cart: React.FC = () => {
  const dispatch = useDispatch();
  const items: Item[] = useSelector(selectCartItems);
  const totalItemsCount = useSelector(selectTotalItemsCount);
  const totalPrice = useSelector(selectTotalPrice);

  return (
    <div className="container container--cart">
      <div className="cart">
        <div className="cart__top">
          <h2 className="content__title">
            <SvgCart />
            –ö–æ—Ä–∑–∏–Ω–∞
          </h2>
          <div
            onClick={() => {
              dispatch(clearItems());
            }}
            className="cart__clear"
          >
            <SvgCartClear />

            <span>–û—á–∏—Å—Ç–∏—Ç–∏ –∫–æ—Ä–∑–∏–Ω—É</span>
          </div>
        </div>
        <div className="content__items">
          {items.map(
            (
              { id, count, title, imageUrl, price, type, size },
              index: number
            ) => (
              <CartItem
                id={id}
                count={count}
                title={title}
                imageUrl={imageUrl}
                price={price}
                type={type}
                size={size}
                key={index}
              />
            )
          )}
        </div>
        <div className="cart__bottom">
          <div className="cart__bottom-details">
            <span>
              {" "}
              –£—Å—å–æ–≥–æ –ø—ñ—Ü–∏: <b>{totalItemsCount} —à—Ç.</b>{" "}
            </span>
            <span>
              {" "}
              –°—É–º–∞ –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è: <b>{totalPrice} ‚Ç¥</b>{" "}
            </span>
          </div>
          <div className="cart__bottom-buttons">
            <Link
              to="/"
              className="button button--outline button--add go-back-btn"
            >
              <SvgGoBack />
              <span>–ü–æ–≤–µ—Ä–Ω—É—Ç–∏—Å—è –Ω–∞–∑–∞–¥</span>
            </Link>

            <div className="button pay-btn">
              <span>–û–ø–ª–∞—Ç–∏—Ç–∏ –∑–∞—Ä–∞–∑</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cart;
